#ourServices .sec3 .service-item {
  background-color: transparent;
  height: 20rem;
  perspective: 1000px;
}

#ourServices .sec3 .service-item .item-container {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 1.4s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  -webkit-transition: transform 1.4s;
  -moz-transition: transform 1.4s;
  -ms-transition: transform 1.4s;
  -o-transition: transform 1.4s;
}


  #ourServices .sec3 .service-item .item-container .front .title-box{
    border: 3px solid white;
    width: 12rem;
    padding: 0rem 2rem;
    height: 7rem;
    text-align: center;
    display: flex;
    align-items: center;
  }


  #ourServices .sec3 .service-item .item-container .front .title-box .service-title{
    text-transform: uppercase;
  }


  
/* the flip*/
#ourServices .sec3 .service-item:hover .item-container {
    transform: rotateY(180deg);
  }
  

#ourServices
  .sec3
  .service-item
  .item-container .front,
  #ourServices
  .sec3
  .service-item
  .item-container .back {
  position: absolute;
  width: 100%;
  min-height: 100%;
  height: auto;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

#ourServices .sec3 .service-item .front {
    color: white;
    justify-content: center;
    align-items: center;
  }
  
  #ourServices .sec3 .service-item .back {
   transform: rotateY(180deg);
   padding: 2rem;
   box-sizing: border-box;
   justify-content: space-between;
   gap: 1.5rem;
  }
  
  #ourServices .sec3 .service-item .back p{
    font-size: 1.3rem;
  }

  #ourServices .sec3 .service-item .back h1,
#ourServices .sec3 .service-item .back p,
#ourServices .sec3 .service-item .back ul {
    margin: 0;
}

  #ourServices .sec3 .service-item .back ul{
    display: flex;
    flex-direction: column;
    padding: 0px;
    list-style-position: inside;
    font-size: 1.3rem;
    gap: 1rem;
}

#ourServices .sec3 .service-item .back ul li {
  display: flex;
}

#ourServices .sec3 .service-item .back ul li:before {
  content: "\2022";
  color: var(--color-secondary);
  font-weight: bold; 
  display: flex; 
  width: 25px; 
  font-size: 2.5rem;
  align-items: center;
  line-height: 0px;
}

#ourServices .sec3 .service-item .back button {
  margin: 0;
  text-transform: uppercase;
  font-weight: 900;
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
  font-size: 1rem;
  width: fit-content;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari, Chrome, Opera, Samsung */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Edge, IE */
  user-select: none; /* Modern browsers */
  touch-action: none;
}

#ourServices .sec3 .service-item .back button:hover{
  opacity: 0.85;
  }

  
  @media only screen and (width < 1000px) {
    #ourServices .sec3 .service-item .back h1{
      font-size: 2rem;
    }
  
  }